.wrapper {
  border-top: 2px solid lightgray;
  padding: 10px;
  min-height: 20px;
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  background-color: white;
}
.textInput {
  width: calc(100% - 20px);
  border: none;
  background-color: inherit;
}
.textInput:focus {
  outline: none;
}
.sendBtn {
  padding: 0px;
  border: none;
  background: transparent;
}
.sendBtn:focus {
  outline: none;
}
.upArrow::before {
  content: '\2191';
  width: 20px;
  height: 20px;
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: #ffffff;
  font-size: 14px;
  font-weight: bold;
  border-radius: 10px;
  background: #006cce;
}
.sendBtn:hover {
  cursor: pointer;
}
.disabled, 
.disabled:hover {
  cursor: not-allowed;
  opacity: 0.5;
}


