.header {
  height: 40px;
  background-color: #0879c9;
  color: #ffffff;
  text-align: center;
  line-height: 40px;
  font-weight: 600;
  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
  cursor: pointer;
}
.minimiseIcon {
  font-size: 18px;
  float: right;
  margin-right: 7px;
  color: #ffffff;
  display: inline-block;
  cursor: pointer;
}
.strechIcon {
  font-size: 18px;
  float: right;
  margin-right: 7px;
  color: #ffffff;
  display: inline-block;
  cursor: pointer;
}
.restoreIcon {
  font-size: 14px;
  float: right;
  margin-right: 7px;
  color: #ffffff;
  display: inline-block;
  cursor: pointer;
}
.hideStretchIcon {
  display: none;
}
.backIcon {
  font-size: 18px;
  color: #ffffff;
  display: inline-block;
  float: left;
  margin-left: 7px;
  cursor: pointer;
}
.hideIcon {
  display: none;
}
.defaultCursor {
  cursor: default;
}



